import React, { useState } from 'react';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import PhotoCamera from '@mui/icons-material/PhotoCamera';
import Stepper from './Stepper';
import styles from './Evaluation.module.css';

const Evaluation = ({ id, questions, formData, handleVotation, handleDraft }) => {
    const handleSubmit = (e) => {
        e.preventDefault();
        handleVotation(id);
    };

    const handleInputChange = (item, value) => {
        handleDraft(id, {...formData, [item]: value});
    };

    const handleFilesChange = (files) => {
        handleDraft(id, {...formData, images: files});
    };

    return (
        <form className={styles.container} onSubmit={handleSubmit}>
            {/* <Stepper></Stepper> */}
            {questions.map((item) => (
                <div className={styles.item} key={item.label}>
                    <div>{item.question}</div>
                    <div className={styles.alternatives}>{item.options.map((opt) => (
                        <>
                        <label for={item.question + opt}>{opt}</label>
                        <input
                            disabled={formData.closed}
                            key={item.question + opt}
                            id={item.question + opt}
                            checked={opt === formData[item.question]}
                            onChange={() => handleInputChange(item.question, opt)}
                            type="radio"
                            name={item.label}
                            value={opt}/>
                        </>
                    ))}</div>
                </div>
            ))}
            {!formData.closed &&
                <div className='clearfix'>
                    <div className={styles.submit}>
                        {formData.images &&
                            formData.images.map((f) => (
                                <span key={`${f.name}`}>{f.name.length > 10 ? f.name.substring(0,10)+'...' : f.name}</span>
                            ))
                        }
                        <IconButton color="primary" aria-label="upload picture" component="label">
                            <input
                                onChange={(e) => {
                                    const filesArray = [...e.target.files];
                                    handleFilesChange(
                                        filesArray.length > 3 ? filesArray.slice(0,3) : filesArray
                                    );
                                }}
                                hidden
                                multiple
                                accept="image/*"
                                type="file" />
                            <PhotoCamera />
                        </IconButton>
                        <Button type="submit" variant="contained">Vote</Button>
                    </div>
                </div>
            }
        </form>
    );
}

export default Evaluation;
